import React, {useState} from 'react';
import TableContainer from "@mui/material/TableContainer";
import Paper from "@mui/material/Paper";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import TableCell from "@mui/material/TableCell";
import TableBody from "@mui/material/TableBody";
import {useAppSelector} from "hooks/hooks";
import {selectCardPacks, selectSortPacks} from "store/selectors/selectPacks";
import {Rating, Table} from "@mui/material";
import s from "pages/packs/Packs.module.css";
import {selectCards, selectSortCards} from "store/selectors/selectCards";
import {CardsActions} from "pages/packs/cards/components/tables/CardsActions/CardsActions";
import {NotFoundPacks} from "pages/packs/components/tables/NotFoundPacks/NotFoundPacks";
import TableSortLabel from "@mui/material/TableSortLabel";
import SortIcon from "@mui/icons-material/Sort";
import {useSort} from "hooks/useSort";
import {rerenderCardsAC, setSortCardsAC} from "store/cardsReducer";
import {selectUserId} from "store/selectors/selectUser";

const TABLE_HEAD_DATA = [
    {
        name: 'Question',
        sortType: 'question',
    },
    {
        name: 'Answer',
        sortType: 'answer',
    },
    {
        name: 'Last Updated',
        sortType: 'updated',
    },
    {
        name: 'Grade',
        sortType: 'grade',
    },
];

export const CardsTable = () => {

    const cards = useAppSelector(selectCards)
    const sortCards = useAppSelector(selectSortCards)
    const userId = useAppSelector(selectUserId)

    const onSortPacksClick = useSort(setSortCardsAC, rerenderCardsAC, selectSortCards);

    return (
        <div className={s.tableContainer}>
            {!cards.length ? <NotFoundPacks
                    messageError={'There are no cards matching the search in this pack'}/> :
            <TableContainer component={Paper}>
                <Table size="small" aria-label="a dense table">
                    <TableHead>
                        <TableRow>
                            {TABLE_HEAD_DATA.map(
                                ({name, sortType}) => <TableCell key={'TableHead-' + name}><TableSortLabel
                                    active={sortCards.slice(1, sortCards.length) === sortType}
                                    direction={sortCards === `0${sortType}` ? 'desc' : 'asc'}
                                    onClick={() => onSortPacksClick(sortType)} IconComponent={SortIcon}>{name}</TableSortLabel></TableCell>
                            )}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {cards.map((card) => (
                            <TableRow key={card._id}>
                                <TableCell component="th" scope="row">
                                    {card.question}
                                </TableCell>
                                <TableCell>{card.answer}</TableCell>
                                <TableCell>{card.updated ? new Date(card.updated).toLocaleString('en-US', {
                                    year: 'numeric',
                                    month: 'long',
                                    day: 'numeric',
                                }) : ''}</TableCell>
                                <TableCell>
                                    <Rating
                                        name="simple-controlled"
                                        value={card.grade}
                                        onChange={() => {
                                        }}
                                    />
                                </TableCell>
                                {/* card actions */}
                                {card.user_id === userId && <TableCell><CardsActions cardId = {card._id} cardUserId = {card.user_id}/></TableCell>}
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer> }
        </div>
    );
};