import React, {useEffect, useState} from 'react';
import s from './Cards.module.css';
import {PATH} from "routes/RoutesComponent";
import {useNavigate, useParams} from "react-router-dom";
import {Button} from "common/components/Button/Button";
import {CardsTable} from "pages/packs/cards/components/tables/CardsTable/CardsTable";
import {CardsFilter} from "pages/packs/cards/components/filters/CardsFilter/CardsFilter";
import {addNewPackTC, getPacksTC} from "store/packsReducer";
import {useAppDispatch, useAppSelector} from "hooks/hooks";
import {
    selectCards, selectCardsPackUserId,
    selectCardsPage,
    selectCardsPageCount,
    selectCardsTotalCount, selectIsUsedCardsFilter,
    selectRerenderFlag, selectSortCards
} from "store/selectors/selectCards";
import {addNewCardTC, getCardsTC, resetCardsAC} from "store/cardsReducer";
import {CustomPagination} from "common/components/CustomPagination/CustomPagination";
import {selectUserId} from "store/selectors/selectUser";
import {CardsOfEmptyPack} from "pages/packs/cards/components/tables/EmptyPack/EmptyPack";
import {Loader} from "common/components/Loader/Loader";
import {selectIsLoader} from "store/selectors/selectApp";

export const Cards = () => {

    const dispatch = useAppDispatch()
    const navigate = useNavigate()

    const isLoader = useAppSelector(selectIsLoader)

    const packId = useParams().packId
    const packName = useParams().packName
    const isMyPack = useParams().isMyPack === 'my-pack'
    const userId = useAppSelector(selectUserId)
    const cards = useAppSelector(selectCards)
    const page = useAppSelector(selectCardsPage)
    const pageCount = useAppSelector(selectCardsPageCount)
    const cardsTotalCount = useAppSelector(selectCardsTotalCount)
    const cardsPackUserId = useAppSelector(selectCardsPackUserId)
    const rerenderFlag = useAppSelector(selectRerenderFlag)
    const sortCards = useAppSelector(selectSortCards)
    const isUsedCardsFilter = useAppSelector(selectIsUsedCardsFilter)

    useEffect(() => {
        if (packId) {
            dispatch(getCardsTC({
                cardsPack_id: packId,
                pageCount: pageCount,
                sortCards: sortCards,
            }))
        }
    }, [rerenderFlag])

    const onChangePage = (page: number, count: number) => {
        let data = {
            page,
            pageCount: count
        }
        dispatch(getPacksTC(data))
    }

    const addNewCard = () => {
        let data = {
            //TODO:  ,  
            card: {
                cardsPack_id: packId,
                question: "blablabla",
                answer: "hehehe",
            }
        }
        dispatch(addNewCardTC(data))
    }

    const handlerBackToPackList = () => {
        navigate(PATH.PACKS)
        dispatch(resetCardsAC())
    }

    return (
        <div className={s.wrapper}>
            <div className={s.backbutton_wrapper}>
                <button className={s.profile_backbutton} onClick={handlerBackToPackList}>
                    Back to Packs List
                </button>
            </div>

            {isLoader && <Loader/>}
            <div className={s.titleContainer}>
                <h2 className={s.sub_title}>{isMyPack ? 'My pack:' : 'Friends pack:'}</h2>
                <h1 className={s.title}>{packName}</h1>
                {cards.length !== 0 && <Button label={"Add new card"} onClick={addNewCard}/>}
            </div>

            {!isUsedCardsFilter && !cards.length ? <CardsOfEmptyPack callback={addNewCard} isMyPack={isMyPack}/>
                : <>
                    <CardsFilter packId={packId ? packId : ''}/>

                    <CardsTable/>

                    <CustomPagination page={page}
                                      itemsCountForPage={pageCount}
                                      totalCount={cardsTotalCount}
                                      onChange={onChangePage}/>
                </>}
        </div>
    );
};