import React, {useState} from "react";
import s from "pages/packs/Packs.module.css";
import {SearchInput} from "common/components/SearchInput/SearchInput";
import {getCardsTC} from "store/cardsReducer";
import {useAppDispatch} from "hooks/hooks";
import SearchIcon from "common/assets/pictures/SearchIcon";

type PropsType = {
    packId: string
}

export const CardsFilter = ({packId}: PropsType) => {

    const [currentInputText, setCurrentInputText] = useState('')

    const dispatch = useAppDispatch()

    const onChangeSearchInput = (value: string) => {

        setCurrentInputText(value)
    }

    const onDebouncedSearchInput = (value: string) => {
        let data = {
            cardsPack_id: packId,
            cardQuestion: value,
            page: 1
        }
        setCurrentInputText(value)
        dispatch(getCardsTC(data))
    }

    return (
        <div className={s.filtersContainer}>
            <div className={s.filtersItem}>
                <div className={s.filtersTitle}>Search by question</div>
                <SearchInput onDebouncedChange={onDebouncedSearchInput}
                             value={currentInputText}
                             onChangeText={onChangeSearchInput} placeholder={"Search by name"}
                             leftIcon={<SearchIcon/>}/>
            </div>
        </div>
    );
};
